// ==========  API ==========
async function callVolcengineAPI(nc: NCItem, section: SectionType, apiKey: string, closingMeetingDate?: string): Promise<string> {
  return queueRequest('volcengine', 'generate', async () => {
    const prompt = buildPrompt(nc, section, closingMeetingDate);

    try {
      // 
      // API Key : ak-xxxxx;sk-xxxxx  Bearer token
      let authorization = apiKey;
      if (!apiKey.startsWith('Bearer ') && !apiKey.includes('ak-')) {
        authorization = `Bearer ${apiKey}`;
      }

      const response = await fetch('https://ark.cn-beijing.volces.com/api/v3/chat/completions', {
        method: 'POST',
        headers: {
          'Authorization': authorization,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          model: 'doubao-pro-32k',
          messages: [
            { role: 'system', content: getSystemInstruction() },
            { role: 'user', content: prompt }
          ],
          temperature: 0.3,
          max_tokens: 4096
        })
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        if (response.status === 404) {
          throw new Error(` API  (404)
1. API Key  ak-xxxxx;sk-xxxxx  Bearer token
2. : doubao-pro-32k
3. : cn-beijing
4. 

: ${JSON.stringify(errorData)}`);
        }
        throw new Error(` API : ${response.status} ${JSON.stringify(errorData)}`);
      }

      const data = await response.json();
      return data.choices?.[0]?.message?.content || '';
    } catch (error) {
      throw error;
    }
  });
}

// ==========  API ==========
async function callSiliconFlowAPI(nc: NCItem, section: SectionType, apiKey: string, closingMeetingDate?: string): Promise<string> {
  return queueRequest('siliconflow', 'generate', async () => {
    const prompt = buildPrompt(nc, section, closingMeetingDate);

    try {
      const response = await fetch('https://api.siliconflow.cn/v1/chat/completions', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${apiKey}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          // 
          model: 'Qwen/Qwen2.5-7B-Instruct',
          messages: [
            { role: 'system', content: getSystemInstruction() },
            { role: 'user', content: prompt }
          ],
          temperature: 0.3,
          max_tokens: 2048, // token
          top_p: 0.9,
          stream: false // 
        })
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(` API : ${response.status} ${JSON.stringify(errorData)}`);
      }

      const data = await response.json();
      return data.choices?.[0]?.message?.content || '';
    } catch (error) {
      throw error;
    }
  });
}

